*:root {
  --orange: #ffaa19;
  --green: #74e8ac;
  --blue: #74c9e8;
  --purple: #b36fdb;
  --grey-900: #4c4d4e;
  --grey-800: #8a8c8e;
  --grey-700: #b7b9bb;
  --grey-600: #c8cbce;
  --grey-500: #dbdee0;
  --grey-100: #f4f5f7; }

.isVisibible {
    visibility: visible; }

.isNotVisible {
      visibility: hidden; }

.body-container {
    background-image: url('./imagenes/splash_paint_black.png');
    background-repeat: no-repeat;
    box-sizing: border-box;
    padding: 2% 5%; }


.font--bold {
  font-weight: bold; }

.font--x-small {
